import React, { PureComponent } from 'react';
import './style.scss';
import Page from '../Page';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { setUser } from '../../../state/duck/user';

import correct       from '../icons/correct.png';
import registration  from '../icons/registration.png';

// import duckling_no   from '../icons/duckling_no.png';
import duckling      from '../icons/duckling.png';

import idea          from '../icons/idea.png';
// import idea_no       from '../icons/idea_no.png';


import list          from '../icons/list.png';
// import list_no       from '../icons/list_no.png';

import mountain      from '../icons/mountain.png';
// import mountain_no   from '../icons/mountain_no.png';

import owl           from '../icons/owl.png';
// import owl_no        from '../icons/owl_no.png';


import target        from '../icons/target.png';
// import target_no     from '../icons/target_no.png';


const achievements = [
  {
    key: '0',
    icon: registration,
    name: 'Registation',
    achieved: true,
  },
  {
    key: '1',
    icon: correct,
    name: 'First TODO',
    achieved: true,
  },
  {
    key: '2',
    icon: duckling,
    name: 'Early riser'
  },
  {
    key: '3',
    icon: idea,
    name: 'Many ideas'
  },
  {
    key: '4',
    icon: list,
    name: 'Done plans'
  },
  {
    key: '5',
    icon: mountain,
    name: 'Sets goals'
  },
  {
    key: '6',
    icon: owl,
    name: 'Night owl'
  },
  {
    key: '7',
    icon: target,
    name: 'Achieve goals'
  },
];


class App extends React.Component {
  constructor(props){
    super(props);

    this.state = {

    };

    this.boundActionCreators = bindActionCreators({
      setUser
    }, props.dispatch);
  }

  logout = () => {
    this.boundActionCreators.setUser(null);
  }

  render() {  
    const { user } = this.props;
    return (
      <Page className='profile_page'>
        {/* <p className='page_title'>Profile</p> */}
        <p className='page_title email'>{user.email}</p>
        <p className='page_title'>Your achievements</p>

        <p className='description'>
          Use â€œZe Tasksâ€ actively and get more achievements.<br/> Find out who is more productive, your friends or you...
        </p>

        <div className='achievements'>

          {
            achievements.map(a => 
            <div className={`achievements_item ${a.achieved ? 'achieved' : ''}`} key={a.key}> 
              <div className='img_container'>
                <img src={a.icon}/>
              </div>
              <span>{a.name}</span>
            </div>)
          }
        </div>

        <div className='center'>
          <button className='btn' onClick={this.logout}>Log out</button>
        </div>
      </Page>
    );
  }
}

const mapStateToProps = ({user, todo}) => ({
  user
});

export default connect(mapStateToProps, null)(App);